import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom'; // React Router useNavigate import
import axios from 'axios'; //    axios import
import '../CSS/Sidebar.css';
import {useAuth} from "../Component/AuthContext"; // CSS  import

const Sidebar = () => {
    const [selectedItem, setSelectedItem] = useState(null); //   
    const [companyName, setCompanyName] = useState(''); //  
    const navigate = useNavigate(); //    useNavigate  
    const { authState, logout, LogoutSuccess, setLogoutSuccess } = useAuth();
    //    ( )
    // useEffect(() => {
    //     const fetchCompanyName = async () => {
    //         try {
    //             // const token = localStorage.getItem('token'); // localStorage  
    //             // if (token) {
    //                 const response = await axios.get('https://hizenberk.github.io/api/users/');
    //                 console.log(response.data)
    //                 setCompanyName(response.data.company_name); //  
    //             // }
    //         } catch (error) {
    //             console.error('  :', error);
    //         }
    //     };
    //
    //     fetchCompanyName().then(r => null); //     
    // }, []);

    //  :      
    const handleClick = (index, path) => {
        setSelectedItem(index); //    
        if (path) {
            navigate(path); //      
        }
    };

    //      
    const menuItems = [
        { name: '', path: '/admin' },
        { name: ' ', path: '/user-management' },
        { name: ' ', path: '/CarHistory' }, //   
        { name: '  ', path: '/trip-history' },
        { name: '', path: '/expense-management' }
    ];

    const settingsMenuItems = [
        { name: '', path: '/settings' } //   
    ];

    return (
        <div className="sidebar">
            <div className="company-section">
                <div className="profile-circle"></div>
                <div className="company-name">{authState.company_name}</div> {/*   */}
            </div>
            <div className="menu">
                <hr />
                {menuItems.map((item, index) => (
                    <div key={index}>
                        <div
                            className={`menu-item ${selectedItem === index ? 'selected' : ''}`} //   'selected'  
                            onClick={() => handleClick(index, item.path)} //     
                        >
                            {item.name} {/*    */}
                        </div>
                        <hr />
                    </div>
                ))}
            </div>
            <div className="settings-menu">
                <hr />
                {settingsMenuItems.map((item, index) => (
                    <div key={index}>
                        <div
                            className={`menu-item ${selectedItem === menuItems.length + index ? 'selected' : ''}`} //   'selected'  
                            onClick={() => handleClick(menuItems.length + index, item.path)} //     
                        >
                            {item.name} {/*     */}
                        </div>
                        <hr />
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Sidebar;
