import React, { useState } from 'react';
import '../CSS/Signup.css';
import axios from "axios";
import {useNavigate} from "react-router-dom";

function Signup() {
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');
  const [businessNumber, setBusinessNumber] = useState('');
  const [companyName, setCompanyName] = useState('');
  const [managerName, setManagerName] = useState('');
  const [managerContact, setManagerContact] = useState('');
  const [email, setEmail] = useState('');
  const [department, setDepartment] = useState('');

  const [error, setError] = useState('');
  const navigate = useNavigate();

  const navigateToLogin = () => {
    navigate("/");
  };
  const handRegister = async (e) => {
    e.preventDefault(); //     
    setError(''); //    
    console.log(' :', "email : " , email, "\n",
        "phone_number : ", managerContact,"\n",
        "password2 :", password,"\n",
        "device_uuid :", "abc123-def456-gh789" ,"\n",
        "company_name :", companyName,"\n",
        "business_registration_number :", businessNumber,"\n",
        "department :", department,"\n",
        "position:", "Developer","\n",
        "name :", managerName
    )
    try {
      const response = await axios.post('https://hizenberk.pythonanywhere.com/api/register/', {
        "email" : email,
        "phone_number": managerContact,
        "password": password,
        "password2": password,
        "device_uuid": "abc123-def456-gh789" ,
        "company_name": companyName,
        "business_registration_number": businessNumber,
        "department": department,
        "position": "Developer",
        "name": managerName
      });
      //     (:  ,  )
      console.log(' :', response.data);
      navigateToLogin();
    } catch (err) {
      console.error(' :', err.response?.data);
      setError(' .   .');
    }
  };



  return (
    <div className="signup-container">
      <form className="signup-form">
        <div className="form-group">
          <input type="text"
                 id="username"
                 name="username"
                 placeholder=""
                 onChange={(e) => setId(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="password"
                 id="password"
                 name="password"
                 placeholder=""
                 onChange={(e) => setPassword(e.target.value)}
          />
          <p className="password-info">,  6 </p>
        </div>
        <div className="form-group">
          <label className="company-info-label"></label>
        </div>
        <div className="form-group">
          <input type="text"
                 id="businessNumber"
                 name="businessNumber"
                 placeholder=""onChange={(e) => setBusinessNumber(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 id="companyName"
                 name="companyName"
                 placeholder=""
                 onChange={(e) => setCompanyName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 id="managerName"
                 name="managerName"
                 placeholder=""
                 onChange={(e) => setManagerName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 id="department"
                 name="department"
                 placeholder=" "
                 onChange={(e) => setManagerContact(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 id="managerDepartment"
                 name="managerDepartment"
                 placeholder="  "
                 onChange={(e) => setDepartment(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="email"
                 id="email"
                 name="email"
                 placeholder=""
                 onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <button type="submit" className="signup-button" onClick={handRegister}></button>
        <p className="terms">
            , <span className="blue-text"> </span>, <span className="blue-text"> </span>, <span
            className="blue-text">   </span>   .
        </p>
      </form>
    </div>
  );
}

export default Signup;
