import React, { useState } from 'react';
import '../CSS/ExpenseManagement.css';

const ExpenseManagement = () => {
  //  
  const dummyData = [
    { id: 1, type: '', date: '9.19', status: '', user: ' /', vehicle: '121234', detail: '', payment: '', amount: '3,421', receipt: '', attachment: '' },
    { id: 2, type: '', date: '9.20', status: '', user: ' /', vehicle: '345678', detail: '', payment: '', amount: '15,300', receipt: '', attachment: '' },
  ];

  //  
  const [rowsPerPage, setRowsPerPage] = useState(10);  //    
  const [currentPage, setCurrentPage] = useState(1);   //  

  //   
  const indexOfLastRow = currentPage * rowsPerPage;
  const indexOfFirstRow = indexOfLastRow - rowsPerPage;
  const currentRows = dummyData.slice(indexOfFirstRow, indexOfLastRow);

  //    
  const totalPages = Math.ceil(dummyData.length / rowsPerPage);

  //   
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  //     
  const handleRowsPerPageChange = (e) => {
    setRowsPerPage(parseInt(e.target.value));
    setCurrentPage(1);  //   
  };

  return (
    <div className="expense-management">
      <main className="main-content">
        <div className="header">
          <h1> </h1>
          <div className="filters">
            <button className="filter-btn">(10)</button>
            <button className="filter-btn">(8)</button>
            <button className="filter-btn">(1)</button>
            <button className="filter-btn">(1)</button>
            <button className="download-btn"> </button>
          </div>
        </div>

        <div className="table-controls">
          <select className="rows-per-page" value={rowsPerPage} onChange={handleRowsPerPageChange}>
            <option value="10">10 </option>
            <option value="20">20 </option>
            <option value="30">30 </option>
          </select>
          <input type="text" className="search-box" placeholder="..." />
        </div>

        <table className="expense-table">
          <thead>
            <tr>
              <th><input type="checkbox" /></th>
              <th></th>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
              <th>  / </th>
              <th> </th>
              <th></th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {currentRows.map((row) => (
              <tr key={row.id}>
                <td><input type="checkbox" /></td>
                <td>{row.type}</td>
                <td>{row.date}</td>
                <td>{row.status}</td>
                <td>{row.user}</td>
                <td>{row.vehicle}</td>
                <td>{row.detail}</td>
                <td>{row.payment}</td>
                <td>{row.amount}</td>
                <td>{row.receipt}</td>
                <td>{row.attachment}</td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="pagination">
          <button
            className="page-btn"
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
          >
            &lt;
          </button>
          {[...Array(totalPages)].map((_, index) => (
            <button
              key={index}
              className={`page-btn ${currentPage === index + 1 ? 'active' : ''}`}
              onClick={() => handlePageChange(index + 1)}
            >
              {index + 1}
            </button>
          ))}
          <button
            className="page-btn"
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            &gt;
          </button>
        </div>
      </main>
    </div>
  );
};

export default ExpenseManagement;
