import React, { useState } from 'react';
import '../CSS/Admin.css';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement, //     
  ArcElement //     
} from 'chart.js';
import PieChart from './PieChart';  // PieChart 
import BarChart from './BarChart';
import {useAuth} from "../Component/AuthContext";  // BarChart 

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement, //   
  ArcElement //   
);

function Admin() {
  const handleClick = (item) => {
    console.log(`${item} `); //    (    )
  };

  const [selectedMonth, setSelectedMonth] = useState('');

  const handleYearChange = (event) => {
    const selectedYear = event.target.value;
    //      
    console.log(` : ${selectedYear}`);
    
    // API      
    fetchDataForYear(selectedYear);
  };
  
  const fetchDataForYear = (year) => {
    //       
    // : API    
    console.log(`${year}  .`);
  };

  //Month Select
  const handleMonthChange = (event) => {
    const selectedMonth = event.target.value;
    //      
    console.log(` : ${selectedMonth}`);
    setSelectedMonth(selectedMonth); //    
    fetchDataForMonth(selectedMonth);
    
    // API      
    fetchDataForMonth(selectedMonth);
  };
  const { authState } = useAuth();
  
  const fetchDataForMonth = (Month) => {
    //       
    // : API    
    console.log(`${Month}  .`);
  };

  return (
    <div className="admin-container">
      
      <div className="admin-top">
        <p className="office-name"> : {authState.company_name}</p>
        <p className="person-name"> {authState.name}  !</p>
      </div>

      <div className="a-box">

        <div className="b-box">

          <div className="d-box">

            <div className="f-box">

              <div className="admin-middle">
                <div type="submit" className="user-info-box">
                  <p className="admin-middle-title"></p>
                  <div className="admin-middle-content">
                    <p className="admin-middle-text"></p>
                    <p className="admin-middle-text"></p>
                    <p className="admin-middle-text"></p>
                  </div>
                </div>

                <div type="submit" className="vehicle-status-box">
                  <p className="admin-middle-title"> </p>
                  <div className="admin-middle-content">
                    <p className="admin-middle-text"> </p>
                    <p className="admin-middle-text"></p>
                    <p className="admin-middle-text">/  </p>
                  </div>
                </div>

                <div type="submit" className="vehicle-run-box">
                  <p className="admin-middle-title"></p>
                  <div className="admin-middle-content">
                    <p className="admin-middle-text">  </p>
                    <p className="admin-middle-text"></p>
                  </div>
                </div>
              </div>

            </div>

            <div className="g-box">

              <div type="submit" className="user-guide-box">
                <p className="admin-middle-title"> </p>
                <div className="user-guide-content">
                  {/*      */}
                </div>
              </div>

            </div>

          </div>

          <div className="e-box">

          <div className="notice-box">
            <p className="notice-title"></p>
            <div className="notice-content">
              {/*     */}
            </div>
          </div>

          </div>
          
        </div>

        <div className="c-box">

          <div className="admin-bottom">
            <div className="operation-status-box">
              <div className="operation-status-top">
                <p className="admin-bottom-title"> </p>
                <select className="year-select" onChange={handleYearChange}>
                  <option value="2024">2024</option>
                  <option value="2023">2023</option>
                  <option value="2022">2022</option>
                  <option value="2021">2021</option>
                </select>
                <select className="month-select" onChange={handleMonthChange}>
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                  <option value="5">5</option>
                  <option value="6">6</option>
                  <option value="7">7</option>
                  <option value="8">8</option>
                  <option value="9">9</option>
                  <option value="10">10</option>
                  <option value="11">11</option>
                  <option value="12">12</option>
                </select>
                <p className="operation-status-top-title">{selectedMonth ? `${selectedMonth} ` : '1 '}</p>
                <p className="month-distance-title"> </p>
                <div className="b-label-box">
                    <div className="d-label" />
                    <p className="label-text"></p>
                    <div className="e-label" />
                    <p className="label-text">/</p>
                    <div className="f-label" />
                    <p className="label-text"></p>
                  </div>
              </div>
              <div className="operation-status-middle">
                <div className="operation-number">
                  <p className="operation-status-middle-title"></p>
                    <div className="op-box">
                     <p className="operation-number-count">1,000</p>
                     <p className="op-count"> </p>
                    </div>
                    <p className="daily-average"> </p>
                    <hr className="custom-line" />
                    <p className="weekly-average"> </p>
                    <hr className="custom-line" />
                    <p className="monthly-average"> </p>
                    <hr className="custom-line" />
                </div>
                <div className="operation-distance">
                  <p className="operation-status-middle-title"></p>
                  <div className="op-box">
                    <p className="operation-number-count">1,000</p>
                    <p className="op-count"> km</p>
                  </div>
                  <p className="daily-average"> </p>
                  <hr className="custom-line" />
                  <p className="weekly-average"> </p>
                  <hr className="custom-line" />
                  <p className="monthly-average"> </p>
                  <hr className="custom-line" />
                </div>
                <div className="operation-percentage">
                  <div className="a-label-box">
                    <div className="a-label" />
                    <p className="label-text"></p>
                    <div className="b-label" />
                    <p className="label-text">/</p>
                    <div className="c-label" />
                    <p className="label-text"></p>
                  </div>
                  <PieChart />
                </div>
                <div className="month-distance">
                  <BarChart />
                </div>

              </div>

            </div>

          </div>
          
        </div>

      </div> 

      

    </div>
  );
}

export default Admin;
