import '../CSS/App.css';
import {BrowserRouter as Router, Route, Routes, Link, useLocation, useNavigate} from 'react-router-dom';
import Signup from './Signup';
import Admin from './Admin';
import React, { useState } from 'react';
import axios from 'axios';
import Sidebar from './Sidebar';
import ExpenseManagement from './ExpenseManagement';
import CarHistory from './CarHistory';
import UserManagement from './UserManagement';
import { useAuth ,AuthProvider } from '../Component/AuthContext';

function App() {
  const location = useLocation(); //   

  const showSidebar = location.pathname !== '/' && location.pathname !== '/signup'; // '/' '/signup'  

  return (
    <div className="App">
      {showSidebar && <Sidebar />} {/*  Sidebar  */}
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="/signup" element={<Signup />} />
        <Route path="/admin" element={<Admin />} />
        <Route path="/expensemanagement" element={<ExpenseManagement />} />
        <Route path="/carhistory" element={<CarHistory />} />
        <Route path="/usermanagement" element={<UserManagement />} />
      </Routes>
    </div>
  );
}

function Login() {
  const [emailOrPhone, setEmailOrPhone] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const { login } = useAuth();
  const navigate = useNavigate();
  const {setLogoutSuccess} = useAuth();
  const navigateToAdmin = () => {
    navigate("/admin");
  };
  const handleLogin = async (e) => {
    e.preventDefault(); //     
    setError(''); //    
    // console.log(' :', "email_or_phone:" ,emailOrPhone,"\n",
    //     "password:", password);
    try {
      const response = await axios.post('https://hizenberk.pythonanywhere.com/api/login/', {
        "email_or_phone": emailOrPhone,
        "password": password,
      });
      setLogoutSuccess('');
      //     (:  ,  )
      console.log(' :', response.data.refresh, "\n",
          response.data.access,"\n",
          response.data.user_info.company_name,"\n",
          response.data.user_info.department,"\n",
          response.data.user_info.name);
      login(
          response.data.refresh,
          response.data.access,
          response.data.user_info.company_name,
          response.data.user_info.department,
          response.data.user_info.name,
      )
      console.log(' :', response.data);
      navigateToAdmin();
    } catch (err) {
      console.error(' :', err.response?.data);
      setError(' .    .');
    }
  };

  return (
    <div className="login-container">
      <form className="login-form" onSubmit={handleLogin}>
        <div className="form-group">
          <input
            type="text"
            id="username"
            name="username"
            placeholder=""
            value={emailOrPhone}
            onChange={(e) => setEmailOrPhone(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="password"
            id="password"
            name="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          {error && <p style={{ color: 'red' }}>{error}</p>}
        </div>
        <div className="form-footer">
          <div className="auto-login">
            <input type="checkbox" id="autoLogin" name="autoLogin" />
            <label htmlFor="autoLogin"></label>
          </div>
          <Link to="/signup" className="signup-label"></Link>

          <button type="submit" className="login-button"></button>
        </div>
      </form>
    </div>
  );
}

export default function Root() {
  return (
      <AuthProvider> {/* AuthProvider App  */}
        <Router>
          <App />
        </Router>
      </AuthProvider>
  );
}
