import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { ko } from 'date-fns/locale'; //  
import '../CSS/CarHistory.css';

const CarHistory = () => {
  const [isDatePickerOpen, setIsDatePickerOpen] = useState(false);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [recordOption, setRecordOption] = useState('');
  const [useType, setUseType] = useState('');
  const [distanceOption, setDistanceOption] = useState(''); //   

  //    (  )
  const carData = [
    {
      id: 1,
      date: "2024-09-24",
      purpose: "",
      car: "K3(1231234)",
      driver: "  / ",
      distance: "6 km / 00:10",
      start: "",
      end: "",
      totalDistance: "123,456 km",
      isDeleted: false,
    },
    {
      id: 2,
      date: "2024-09-25",
      purpose: "",
      car: "K5(4567890)",
      driver: "  / ",
      distance: "12 km / 00:20",
      start: "",
      end: "",
      totalDistance: "123,500 km",
      isDeleted: false,
    },
    {
      id: 3,
      date: "2024-09-26",
      purpose: "",
      car: "(7891234)",
      driver: "  / ",
      distance: "8 km / 00:15",
      start: "",
      end: "",
      totalDistance: "123,800 km",
      isDeleted: true,
    },
  ];

  // km   
  const getKmValue = (distance) => {
    return parseFloat(distance.split(' km')[0]);
  };

  //         
  const filterCarData = () => {
    return carData.filter((car) => {
      //    
      if (recordOption === '  ' && car.isDeleted) return false;
      //   
      if (useType !== '' && car.purpose !== useType) return false;
      //   
      const carKm = getKmValue(car.distance);
      if (distanceOption === '3km ' && carKm < 3) return false;
      if (distanceOption === '3km ' && carKm > 3) return false;
      return true;
    });
  };

  //         
  const handleRecordOptionChange = (e) => {
    setRecordOption(e.target.value);
  };

  //        
  const handleUseTypeChange = (e) => {
    setUseType(e.target.value);
  };

  //         
  const handleDistanceOptionChange = (e) => {
    setDistanceOption(e.target.value);
  };

  //   
  const handleReset = () => {
    setStartDate(null);
    setEndDate(null);
  };

  return (
    <div className="container">
      <div className="carhistory">
        <header className="header">
          <div className="carhistory-name">  </div>
          <button className="download-btn"> </button>
        </header>
        <hr className="divider"/>    
      </div>

      <div className="filters">
        <div className="filter-row">
          <label> :</label>
          <input
            type="radio"
            name="record-option"
            value=""
            checked={recordOption === ''}
            onChange={handleRecordOptionChange}
          /> 
          <input
            type="radio"
            name="record-option"
            value="  "
            checked={recordOption === '  '}
            onChange={handleRecordOptionChange}
          />   
        </div>

        <div className="1">
        <div className="aa">
          <label> :</label>
          <input
            type="radio"
            name="use-type"
            value=""
            checked={useType === ''}
            onChange={handleUseTypeChange}
          /> 
          <input
            type="radio"
            name="use-type"
            value=""
            checked={useType === ''}
            onChange={handleUseTypeChange}
          /> /
          <input
            type="radio"
            name="use-type"
            value=""
            checked={useType === ''}
            onChange={handleUseTypeChange}
          /> 
          <input
            type="radio"
            name="use-type"
            value=""
            checked={useType === ''}
            onChange={handleUseTypeChange}
          /> 
        </div>
      </div>

        <div className="filter-row">
          <label> :</label>
          <input
            type="radio"
            name="distance-option"
            value=""
            checked={distanceOption === ''}
            onChange={handleDistanceOptionChange}
          /> 
          <input
            type="radio"
            name="distance-option"
            value="3km "
            checked={distanceOption === '3km '}
            onChange={handleDistanceOptionChange}
          /> 3km 
          <input
            type="radio"
            name="distance-option"
            value="3km "
            checked={distanceOption === '3km '}
            onChange={handleDistanceOptionChange}
          /> 3km 
        </div>

        <div className="bb">
          <label> :</label>
          <button onClick={() => setIsDatePickerOpen(!isDatePickerOpen)}>
             
          </button>
        </div>
        <div className='cc'>
          <label>:</label>
          <input></input>
        </div>
      </div>

      {/* Date picker modal */}
      {isDatePickerOpen && (
        <div className="date-picker-popup">
          <h4> </h4>

    {/*    DatePicker  */}
    <div className="date-picker-container">
          <DatePicker
            locale={ko} //  
            selected={startDate}
            onChange={(dates) => {
              const [start, end] = dates;
              setStartDate(start);
              setEndDate(end);
            }}
            startDate={startDate}
            endDate={endDate}
            selectsRange
            inline
            showMonthDropdown  //    
            showYearDropdown   //    
            dropdownMode="select"  //   / 
          />

          <DatePicker
            locale={ko} //  
            selected={endDate}
            onChange={(dates) => {
              const [start, end] = dates;
              setStartDate(start);
              setEndDate(end);
            }}
            startDate={startDate}
            endDate={endDate}
            selectsRange
            inline
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          />
    </div>

          <div className="date-display">
            <p> : {startDate ? startDate.toLocaleDateString('ko-KR') : ' '}</p>
            <p> : {endDate ? endDate.toLocaleDateString('ko-KR') : ' '}</p>
          </div>
          <button className="reset-button" onClick={handleReset}> </button>
          <button onClick={() => setIsDatePickerOpen(false)}></button>
        </div>
      )}


      <table className="car-history-table">
        <thead>
          <tr>
            <th>/</th>
            <th></th>
            <th>()</th>
            <th> / ()</th>
            <th>/</th>
            <th> </th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {filterCarData().map((car) => (
            <tr key={car.id}>
              <td>{car.date}</td>
              <td>{car.purpose}</td>
              <td>{car.car}<br />{car.driver}</td>
              <td>{car.distance}</td>
              <td>: {car.start}<br />: {car.end}</td>
              <td>{car.totalDistance}</td>
              <td><button></button></td>
              <td><button> </button></td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default CarHistory;
